.todo-app {
  position: relative;
  display: inline-table;
  flex-direction: column;
  justify-content: start;
  min-width: 400px;
  width: 28%;
  height: 550px;
  background: #161a2b;
  text-align: center;
  margin: 12px 20px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);
  /* box-shadow:5px 5px 50px -14px black; */
  border-radius: 10px;
  padding-bottom: 32px;
  border: 2px double transparent;
  background-image: linear-gradient(rgb(13, 14, 33), rgb(13, 14, 33)), radial-gradient(circle at left top, rgba(93, 12, 255, 0.726), rgba(154, 0, 250, 0.712));
  /* background-image: linear-gradient(rgb(13, 14, 33), rgb(13, 14, 33)), radial-gradient(circle at left top, rgba(93, 12, 255, 1), rgba(155, 0, 250, 1)); */
  background-origin: border-box;
  background-clip: padding-box, border-box;
}


.extra-padding {
  padding-bottom: 95px;
}


@media only screen and (max-width: 435px){
  .todo-app {
    min-width: 90vw;
  }
}


.todo-heading {
